@import url(https://fonts.googleapis.com/css2?family=Mate+SC&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Myeongjo:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.row-title {
   font-size: 2rem;
   font-weight: 500;
}
.row {
   font-family: 'Mate SC', serif;
   margin: 25px;
   margin-bottom: 0px;
   color: #fff;
}
.alert {
   font-size: 1.2rem;
   padding: 1rem 2.4rem;
   letter-spacing: 1px;
   margin-top: 0.5rem;
   background-color: rgba(0, 0, 0, 0.8);
   color: #fff;
   transition-timing-function: ease-in;
   transition-duration: 0.2s;
}
.alert i {
   margin-right: 1rem;
}
.row_posters {
   display: -webkit-flex;
   display: flex;
   overflow-y: hidden;
   overflow-x: scroll;
   padding: 20px;
}
.row_posters::-webkit-scrollbar {
   display: none;
}
.poster {
   width: 100%;
   cursor: pointer;
   border-radius: 8px;
   object-fit: contain;
   max-height: 123px;
   margin-right: 15px;
   transition: all;
   transition-timing-function: inherit;
   transition-duration: 0.4s;
}

.poster:hover {
   -webkit-transform: scale(1.07);
           transform: scale(1.07);
}

.large_poster {
   max-height: 250px;
}
.large-poster:hover {
   -webkit-transform: scale(1.1);
           transform: scale(1.1);
}
@media only screen and (max-width: 500px) {
   .alert {
      font-size: 0.8rem;
      padding: 1rem;
   }
}

* {
   margin: 0;
}

.App {
   background-color: #111;
}

.nav {
   position: fixed;
   height: 30px;
   top: 0;
   z-index: 1;
   display: -webkit-flex;
   display: flex;
   width: 100%;
   padding: 20px;
   -webkit-justify-content: space-between;
           justify-content: space-between;
   transition-timing-function: ease-in;
   transition-duration: 0.4s;
}
.nav_black {
   background-color: rgba(0, 0, 0, 0.9);
}
.nav_logo {
   position: fixed;
   left: 20px;
   width: 95px;
   object-fit: contain;
}

.nav_avatar {
   width: 30px;
   position: fixed;
   right: 20px;
   object-fit: contain;
}

.banner {
   color: #fff;
   object-fit: contain;
   height: 448px;
   background-repeat: no-repeat;
   padding-bottom: 0;
}

.banner_content {
   margin-left: 30px;
   padding-top: 140px;
   height: 190px;
}
.banner_title {
   font-family: 'Nanum Myeongjo', serif;
   font-size: 3rem;
   font-weight: 800;
   padding-bottom: 0.3rem;
}

.banner_description {
   width: 45rem;
   max-width: 360px;
   line-height: 1.3;
   font-size: 0.9rem;
   padding-top: 1rem;
   height: 80px;
}

.banner_btn {
   color: #fff;
   font-weight: 700;
   border: none;
   outline: none;
   cursor: pointer;
   border-radius: 0.2vw;
   padding: 0.5rem 2rem;
   margin-right: 1rem;
   background-color: rgba(51, 51, 51, 0.5);
   transition: all 0.3s;
   transition-timing-function: inherit;
}
.banner_btn:hover {
   color: #000;
   background-color: #e6e6e6;
}

.banner_fadeBottom {
   height: 7.4rem;
   background-image: linear-gradient(
      180deg,
      transparent,
      rgba(37, 37, 37, 0.61),
      #111
   );
}

@media only screen and (max-width: 500px) {
   .banner_title {
      font-size: 2rem;
   }
   .banner_description {
      max-width: 280px;
   }
}

